*{
  margin: 0px;
  padding: 0px;
  outline: none;
}
.navbar{
  --bs-navbar-padding-x: 0.8;
}
.navbar .container-fluid{
  /* position:relative; */
  position: absolute;
  display: flex;
  justify-content: space-between;

}
.navbar-nav .nav-item .nav-link {
  color:white;
  position: relative; /* Add position relative to create a reference for the pseudo-element */
}

/* Style for the underline */
.navbar-nav .nav-item .nav-link::after{
  content: ''; /* Create a pseudo-element */
  position: absolute; /* Position it absolutely */
  bottom: 0; /* Align at the bottom */
  left: 0; /* Align at the left */
  width: 100%; /* Full width */
  height: 3px; /* Height of the underline */
  /* background: linear-gradient(rgba(100, 200, 300), rgba(400, 100, 500)); Gradient background */ 
  transition: all 0.3s ease; /* Smooth transition for underline */
  opacity: 0; /* Initially invisible */
  background-color:white;
  
  
}

/* Show the underline on hover */
.navbar-nav .nav-item .nav-link:hover::after{
  opacity: 1; /* Make the underline visible on hover */
 
 
}
.navbar-nav .nav-item .nav-link:hover {
  color: chartreuse;
 font-size: 20px;
 text-shadow: 2px 2px black;
  font-family: italic;
} 

