.navbar-nav .nav-item {
  position: relative; /* Position relative for parent */
  
}

.bran {
  width: 250px;
  background-color: olivedrab;
  position: absolute;
  display: none;
  top: calc(100% + 5px); /* Position below the nav item */
  left: 50%; /* Position the left of the .bran container at 50% of the parent */
transform: translateX(-50%); 
  padding: 10px;
  color: white;
  

}
.bran h6{
  /* margin-top: 20px; */
