}

.nav-item:hover .bran {
  display: block; /* Show the .bran container on hover */

}
.mang{
  background-color: olivedrab;
  position: absolute;
  width: 150px;
  left: 50%; /* Position the left of the .bran container at 50% of the parent */
transform: translateX(-50%); 
